body {
    margin: 0;
    padding: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    user-select: none;
}

input,
button {
    padding: 5px;
    margin: 5px;
    width: calc(100% - 10px);
    display: block;
    box-sizing: border-box;
    outline: none;
}



.back-button {
    top: 10px;
    left: 10px;
    position: absolute;
}



.auth-form {
    margin: auto;
    width: 300px;
    text-align: center;
}



.games-list {
    margin: auto;
    width: 300px;
    text-align: center;
}

.games-item {
    margin: 20px 0;
    border: 1px solid #000;
    border-radius: 20px;
}

.games-page {
    margin: auto;
    width: 300px;
    text-align: center;
}

.games-page-box {
    margin: 20px 0;
    padding: 10px 30px;
    border: 1px solid #000;
    border-radius: 20px;
}

.games-page-box h2,
.games-page-box p,
.games-page-box button {
    margin: 5px;
    padding: 5px;
}